body {
  min-width: 320px;
  margin: 0;
  font-family: 'Fira Sans', sans-serif;
  color: #191919;
}
* {
  box-sizing: border-box;
}
*:before,
*:after {
  box-sizing: border-box;
}
html {
  font-size: 14px;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}
input,
